<template>
  <div class="img-ai">
    <div class="input-box">
      <vs-input v-model="inputValue" @keyup.enter="getRes" placeholder="" />
      <vs-button
          @click="getRes"
      >
        
      </vs-button>
    </div>
    <div class="res-box">
      <img class="res-img" :src="img.url" alt="">
    </div>

    <vs-dialog loading v-model="isLoading">
      <template #header>
        <h4 class="not-margin">
          AI
        </h4>
      </template>


    </vs-dialog>
  </div>
</template>

<script>
const {Configuration, OpenAIApi} = require("openai");
const configuration = new Configuration({
  apiKey: process.env.VUE_APP_API_KEY,
});
const openai = new OpenAIApi(configuration);

export default {
  name: "img-ai",
  data() {
    return {
      inputValue:"",
      img:[],
      isLoading:false
    }
  },
  methods: {
    async getRes() {
      this.isLoading = true
      const response = await   openai.createImage({
        prompt: this.inputValue,
        n: 1,
        size: "1024x1024",
      });

      this.img =response.data.data[0]
      this.isLoading =false
      this.inputValue=""
    }
  }
}
</script>

<style lang="scss" scoped>
.img-ai{
  width: 100vw;
  .input-box{
    display: flex;
    justify-content: center;
    margin:  2em auto;
  }
  .res-box{
    padding: 0 10vw;
    .res-img{
      width: 100%;
    }
  }
}
</style>
